import React, { useState } from 'react'
import RateEx from '../components/RateEx'
import LatestEx from '../components/LatestEx'
import Navbar from '../components/Navbar'
import {TabContent, TabPane } from 'reactstrap';


import "bootstrap/dist/css/bootstrap.min.css";


export default function Home() {

    // Define state variables using the useState hook
    const [show, setShow] = useState(true)
    const [showLatestEx, setShowLatestEx] = useState(true)
    const [activeTab, setActiveTab] = React.useState('Dashboard');


    /**
     * Toggles between tabs.
     * @param tab - The tab to switch to.
     * @param activeTab - The currently active tab.
     * @param toggleTab - Callback function to toggle between tabs.
     */
    const toggleTab = (tab) => {
        if (activeTab !== tab) {
            setActiveTab(tab);
        }
    };

    return (
        <>
            <Navbar
                activeTab={activeTab}
                toggleTab={toggleTab}
            />

            <TabContent activeTab={activeTab}>
                <TabPane tabId="Dashboard">
                    <div className="container mt-4">
                        <h1>Dashboard</h1>
                        <div className="row">
                            <div className="col-md-6">
                                {
                                    show ?
                                        <div className="card">
                                            <div className="card-body">
                                                <h5 className="card-title">Chart 1</h5>
                                                <RateEx />
                                            </div>
                                        </div> : null
                                }
                            </div>
                            <div className="col-md-6">
                                {showLatestEx ?
                                    <div className="card">
                                        <div className="card-body">
                                            <h5 className="card-title">Chart 2</h5>
                                            <LatestEx />
                                        </div>
                                    </div> : null
                                }
                            </div>
                        </div>
                    </div>
                </TabPane>


                <TabPane tabId="Edit Dashboard">
                    <div className="container mt-4">
                        <h1>Edit Dashboard</h1>

                        <div className="row">
                            <div className="col-md-6">
                                {
                                    show ?
                                        <div className="card">
                                            <div className="card-body">
                                                <h5 className="card-title">Chart 1</h5>
                                                <RateEx />
                                            </div>
                                        </div> : null
                                }
                                <button onClick={() => setShow(!show)} className="btn btn-primary">Show/hide</button>
                            </div>


                            <div className="col-md-6">
                                {showLatestEx ?
                                    <div className="card">
                                        <div className="card-body">
                                            <h5 className="card-title">Chart 2</h5>
                                            <LatestEx />
                                        </div>
                                    </div> : null
                                }
                                <button onClick={() => setShowLatestEx(!showLatestEx)} className="btn btn-primary">Show/hide</button>
                            </div>
                        </div>

                    </div>
                </TabPane>
            </TabContent>

        </>
    )
}
