import React from "react";
import { Nav, NavItem, NavLink} from 'reactstrap';

export default function Navbar( {activeTab, toggleTab} ) {


    return (
        <Nav tabs>
          <NavItem>
            <NavLink
                      /**
                       * Sets the className to 'active' if activeTab matches 'Dashboard or Edit Dashboard'.
                       * Calls the toggleTab function with 'Dashboard or Edit Dashboard' as the argument when clicked.
                       */
              className={activeTab === 'Dashboard' ? 'active' : ''}
              onClick={() => toggleTab('Dashboard')}
            >
              Dashboard
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={activeTab === 'Edit Dashboard' ? 'active' : ''}
              onClick={() => toggleTab('Edit Dashboard')}
            >
              Edit Dashboard
            </NavLink>
          </NavItem>
        </Nav>
    )

}