import axios from "axios";
import { useEffect, useState } from "react";
import {
    Chart as ChartJS, ArcElement, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip,
    Legend, Filler
} from "chart.js"
import Chart from "chart.js/auto";
import { Line } from "react-chartjs-2"

Chart.register(CategoryScale);

ChartJS.register(
    ArcElement,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    Filler
)

/**
 * Fetches exchange rate data for the last month and renders
 * a Line chart displaying the exchange rate from USD to EUR.
 */
function RateEx() {
    // Define state variables using the useState hook
    const [chartData, setChartData] = useState({})

    const apiKey = 'dXfWwMlLmKRolJd2JsbtWgHHiYyyhkfz'

    // Get the current date and the start date (1 month ago)
    const today = new Date(); // Current date
    const startDate = new Date();
    startDate.setMonth(startDate.getMonth() - 1); // Subtract 1 month from the current date

    // Format the start and end dates as YYYY-MM-DD to adhere to the API rules.
    const formattedStartDate = startDate.toISOString().slice(0, 10);
    const formattedEndDate = today.toISOString().slice(0, 10); 

    // Use the useEffect hook to fetch data when the component mounts
    useEffect(() => {
        const fetchData = async () => {
            const { data } = await axios.get(`https://api.apilayer.com/exchangerates_data/timeseries?apikey=${apiKey}&start_date=${formattedStartDate}&end_date=${formattedEndDate}&symbols=EUR&base=USD`)
            console.log(data.rates['EUR'])

            const rates = data.rates

            // Create an array to store the exchange rates with dates
            const exchangeRates = [];

            // Iterate over the rates and populate the exchangeRates array
            for (const date in rates) {
                if (rates.hasOwnProperty(date)) {
                    const rateValue = rates[date].EUR;
                    exchangeRates.push({ date, rateValue })
                }
            }
            console.log(exchangeRates);

            // Passing the fetched data and chart params to component's state using setChartData
            setChartData({
                labels: exchangeRates.map((item) => item.date),
                datasets: [
                    {
                        labels: "Exchange Rate",
                        data: exchangeRates.map(item => item.rateValue),
                        fill: true,
                        borderColor: "rgb(255, 99, 132)",
                        backgroundColor: "rgb(255, 99, 132, 0.3)"
                    }
                ]
            })
        }
        fetchData()
    }, [])
    return (
        <div className="RateEx">
            <div className="chart">
                {chartData && chartData.datasets && (
                    /**
                     * Renders a Line chart using the chartData as the data source and the chart
                     * will only be rendered if chartData and chartData.datasets are truthy.
                     */
                    <Line
                    data={chartData}
                    options={{
                        responsive: true,
                        plugins: {
                            legend: { position: "top" },
                            title: { display: true, text: "Exhange Rate From USD to EUR" }
                        }
                    }}
                />
                )}
            </div>

        </div>
    );
}

export default RateEx