import axios from "axios";
import { useEffect, useState } from "react";
import {
    Chart as ChartJS, ArcElement, CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip,
    Legend, Filler
} from "chart.js"
import { Bar } from "react-chartjs-2"

ChartJS.register(
    ArcElement,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
    Filler
)

/**
 * Fetches the latest exchange rates data and renders a Bar chart displaying
 * the USD exchange rate in EGP, EUR and GBP.
 */
function LatestEx() {

    // Define state variables using the useState hook
    const [chartData, setChartData] = useState({})

    const apiKey = 'dXfWwMlLmKRolJd2JsbtWgHHiYyyhkfz'



    // Use the useEffect hook to fetch data when the component mounts
    useEffect(() => {
        // Define an async function to fetch the data
        const fetchData = async () => {
            // Make a GET request to the exchange rates API
            const { data } = await axios.get(`https://api.apilayer.com/exchangerates_data/latest?apikey=${apiKey}&symbols=EGP%2C%20EUR%2C%20GBP&base=USD`)
            console.log(Object.values(data.rates))

  
            // Passing the fetched data and chart params to component's state using setChartData
            setChartData({
                labels: ['EGP', 'EUR', 'GBP'],
                datasets: [
                    {
                        labels: "Exchange Rate1",
                        data: data.rates,
                        fill: true,
                        borderColor: "rgb(255, 99, 132)",
                        backgroundColor: "rgb(255, 99, 132, 0.3)"
                    }
                ]
            })
        }
        fetchData()
    }, [])
    return (
        <div className="LatestEx">
           {chartData && chartData.datasets && (
             /**
             * Renders a Bar chart using the chartData as the data source and the he chart will
             * only be rendered if chartData and chartData.datasets are truthy.
             */
              <Bar
              data={chartData}
              options={{
                plugins: {
                  title: {
                    display: true,
                    text: "USD price in EGP, EUR and GBP"
                  },
                  legend: {
                    display: false
                  }
                }
              }}
            />
      )}
        </div>
    );
}

export default LatestEx